import React from 'react'
import config from '../../../config'
import { Link } from 'gatsby'
import PreFooter from '../PreFooter'

const Footer = ({ includePreFooter = false }) => {
	return (
		<div className='flex mt-8 bg-white'>
			<div className='container px-8 mx-auto'>
				{includePreFooter &&
				<PreFooter />}
				<footer className='items-center hidden h-12 text-xs text-gray-500 sm_flex'>
					<div className='flex-shrink text-left'>
						{config.copyright}
					</div>
					<div className='flex-grow flex-shrink-0 text-right'>
						<Link className='block inline-block mr-6 hover_underline' to='/privacy'>
							Privacy Policy
						</Link>
						<Link className='block inline-block hover_underline' to='/tos'>
							Terms of Service
						</Link>
					</div>
				</footer>

			</div>
		</div>
	)
}

export default Footer
